
import {ref, defineComponent, onMounted} from 'vue';
import * as monaco from 'monaco-editor';
import {languages, editor, CancellationToken} from 'monaco-editor';
import Panel from '../core/Panel';
import {minify} from 'html-minifier-terser';
// eslint-disable-next-line camelcase
import {html_beautify} from 'js-beautify';
// @ts-ignore
import {XMLParser, XMLBuilder, XMLValidator} from 'fast-xml-parser';
// @ts-ignore
import * as  lodash from 'lodash';
import Msg from '../message';

export default defineComponent({
  name: 'App',
  components: {},
  setup(props, { emit }) {
    let panel: Panel | null = null;
    const editor = ref();

    let sendXMLDataToEngine = lodash.debounce((value: string) => {
      console.log(value);
      panel?.dispatch({panel: Msg.Main.Name, action: Msg.Main.MsgUpdateRichText, data: { data: value }});
    }, 1000, {
      leading: false,
      trailing: true,
    });

    onMounted(async () => {
      // monaco.editor.setTheme('vs-dark');
      const textEditor = monaco.editor.create(editor.value, {
        model: null,
        automaticLayout: true,
        minimap: {
          enabled: false,
        },
      });
      textEditor.trigger('anyString', 'editor.action.formatDocument', ()=>{
        console.log('trigger');
      });
      const keys = ['font', 'i', 'b', 'del', 'u', 'big', 'small', 'a', 'br', 'outline', 'img', 'shadow', 'glow'];
      panel = new Panel('rich-text');
      let ret = await panel.init();
      function updateText(str:string) {
        // str =  htmlFormat(str);
        str = html_beautify(str, {
          // eslint-disable-next-line camelcase
          indent_size: 2,
          // eslint-disable-next-line camelcase
          end_with_newline: true,
          // eslint-disable-next-line camelcase
          preserve_newlines: false, // 保留现有的换行符
          // eslint-disable-next-line camelcase
          wrap_attributes: 'auto',
          // eslint-disable-next-line camelcase
          extra_liners: keys,
        });
        textEditor.setValue(str);
        textEditor.getAction('editor.action.formatDocument').run();
      }
      const newModel = monaco.editor.createModel('',          'xml');
      textEditor.setModel(newModel);
      panel.messages[Msg.RichText.MsgUpdateRichText] = (data: Record<string, any>) => {
        updateText(data.xml as string);
      };
      panel.dispatch({panel: Msg.Main.Name, action: Msg.Main.MsgGetRichText});
      if (ret !== 0) {
        console.error('panel init failed');
      }

      updateText('<img color="#ff0000">test</img>');

      textEditor.onDidChangeModelContent(async () => {
        let val = textEditor.getValue();
        try {
          let ret = await minify(val, {
            removeTagWhitespace: false, // 尽可能删除属性之间的空格
            collapseWhitespace: true,
            collapseInlineTagWhitespace: true, // 折叠空白tag
            conservativeCollapse: false, // 总是折叠到1个空格
          });
          sendXMLDataToEngine(ret);
        } catch (e) {
          console.error(e);
        }
      });


      monaco.languages.registerColorProvider('xml', {
        provideDocumentColors(model: editor.ITextModel,
          token: monaco.CancellationToken):
            languages.ProviderResult<languages.IColorInformation[]> {
          return null;
        },
        provideColorPresentations(model: editor.ITextModel,
          colorInfo: languages.IColorInformation, token: CancellationToken):
            languages.ProviderResult<languages.IColorPresentation[]> {
          return null;
        },
      });
      monaco.languages.registerCompletionItemProvider('xml', {
        triggerCharacters: ['<'],
        provideCompletionItems(
          model: monaco.editor.ITextModel,
          position: monaco.Position,
          context: monaco.languages.CompletionContext,
          token: monaco.CancellationToken): monaco.languages.ProviderResult<monaco.languages.CompletionList> {

          const textUntilPosition = model.getValueInRange({
            startLineNumber: position.lineNumber,
            startColumn: 1,
            endLineNumber: position.lineNumber,
            endColumn: position.column,
          });


          function createCompleter(key: string) {
            let range: monaco.IRange = {
              startLineNumber: position.lineNumber,
              startColumn: position.column,
              endLineNumber: position.lineNumber,
              endColumn: position.column,
            };
            let item: monaco.languages.CompletionItem = {
              label: key,
              kind: monaco.languages.CompletionItemKind.Keyword,
              documentation: key,
              insertText: key,
              range,
            };
            return item;
          }

          return {
            suggestions: keys.map((key) => createCompleter(key)),
          };
        },
      });
    });

    return {
      editor,
    };
  },
});
